import React from 'react';
import { getAll, get5First, getRedGoods } from './api/goods';
import './App.scss';

class App extends React.Component {
  state = {
    goods: [],
  }

  getGoodsFromServer = (action) => {
    action().then(goods => (
      this.setState({ goods })
    ));
  }

  render() {
    const { goods } = this.state;
    const { getGoodsFromServer } = this;

    return (
      <>
        <h1>Dynamic list of Goods</h1>

        <button
          onClick={() => getGoodsFromServer(getAll)}
          type="button"
        >
          Show list
        </button>
        <button
          onClick={() => getGoodsFromServer(get5First)}
          type="button"
        >
          Show 5 first
        </button>
        <button
          type="button"
          onClick={() => getGoodsFromServer(getRedGoods)}
        >
          Show only red
        </button>
        <ul>
          {
            goods.map(good => (
              <li
                key={good.id}
                style={{ color: good.color }}
              >
                { good.name }
              </li>
            ))
          }
        </ul>
      </>
    );
  }

}

export default App;
